@charset "UTF-8";
.wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, auto);
  background-color: #fff;
  padding: 1em;
}

.title-wrapper-holder {
  grid-column: 1/span 4;
  grid-row: 1;
  height: 1rem;
}

.title-wrapper {
  background: #fff;
  grid-column: 1/span 4;
  grid-row: 1;
  display: flex;
  flex-flow: row;
  display: grid;
  transition: 0.2s;
  z-index: 20;
}
@media only screen and (min-width: 60.25em) {
  .title-wrapper {
    grid-template-columns: 12rem auto 19rem;
  }
}
.title-wrapper .back {
  grid-column: 1/span 1;
  font-size: 0.9em;
}
.title-wrapper .back svg {
  margin-right: 1rem;
}
.title-wrapper img {
  width: 100%;
  max-height: 10rem;
  object-fit: cover;
  padding: 0 1em;
}
.title-wrapper .title {
  grid-column: 1/span 4;
  padding: 1em 1em 0 1em;
}
@media only screen and (min-width: 60.25em) {
  .title-wrapper .title {
    grid-column: 2/span 1;
    padding: 0 1em;
  }
}
.title-wrapper .title .affiliation {
  font-style: italic;
}
.title-wrapper .cta-wrapper {
  grid-column: 2/span 2;
  grid-row: 1;
  width: 25%;
}
@media only screen and (min-width: 60.25em) {
  .title-wrapper .cta-wrapper {
    grid-column: 3/span 1;
    grid-row: 1;
  }
}
.title-wrapper .cta-wrapper .stats {
  font-style: italic;
  font-size: 0.85em;
  text-align: center;
  margin: 0.5em 0;
}
.title-wrapper .cta-wrapper .stats p {
  margin: 0;
}
.title-wrapper .cta-wrapper .cta-buttons {
  text-align: center;
  font-size: inherit;
}
.title-wrapper .cta-wrapper .cta-buttons p {
  display: inline-block;
  margin: 0.2ems;
}
.title-wrapper .cta-wrapper .cta-buttons .btn-group {
  font-size: 100%;
}
.title-wrapper .cta-wrapper .cta-buttons .btn-group > .btn + .btn {
  margin-left: -10px;
}

.isSticky {
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.5);
  font-size: 0.8rem;
  padding-top: 2em;
}
.isSticky h2 {
  font-size: 1.7rem;
}
.isSticky .stats {
  display: none;
}
.isSticky img {
  max-height: 5rem;
  object-fit: contain;
  padding-bottom: 0.5em;
}

.content-top {
  grid-column: 1/span 4;
  grid-row: 2;
  display: flex;
  flex-flow: row;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, auto);
}
@media only screen and (min-width: 35em) {
  .content-top {
    grid-template-rows: repeat(2, auto);
  }
}
@media only screen and (min-width: 60.25em) {
  .content-top {
    grid-template-columns: 12rem auto 19rem;
    grid-template-rows: auto;
  }
}
.content-top .image-wrapper {
  grid-column: 1/span 2;
  grid-row: 3;
  flex-flow: column;
  padding: 1em;
  width: 20%;
}
@media only screen and (min-width: 35em) {
  .content-top .image-wrapper {
    grid-column: 1/span 1;
    grid-row: 2;
  }
}
@media only screen and (min-width: 60.25em) {
  .content-top .image-wrapper {
    grid-column: 1/span 1;
    grid-row: 1;
  }
}
.content-top .image-wrapper img {
  width: 100%;
  max-height: 10rem;
  object-fit: cover;
}
.content-top .image-wrapper .share {
  margin: 1em 0;
}
.content-top .image-wrapper .share .fa {
  margin-right: 0.2em;
}
.content-top .image-wrapper .share .icon-collect:before {
  content: "\f005";
  font-family: "Fontcons";
}
.content-top .image-wrapper .share a {
  margin: 0.5em 0;
}
.content-top .abstract-wrapper {
  grid-column: 1/span 2;
  grid-row: 1;
  width: 60%;
  padding: 1em;
}
@media only screen and (min-width: 60.25em) {
  .content-top .abstract-wrapper {
    grid-column: 2/span 1;
  }
}
.content-top .abstract-wrapper .more-content:before {
  content: "âŒ„";
  font-family: "Fontcons";
  margin-right: 0.3em;
}
.content-top .abstract-wrapper .meta-top img {
  width: 5rem;
}
.content-top .abstract-wrapper .tags h4 {
  font-size: 1rem;
}
.content-top .abstract-wrapper .high-level-tag-wrapper {
  margin-top: 1em;
}
.content-top .abstract-wrapper .high-level-tag-wrapper .high-level-tag {
  display: inline-block;
  font-size: 0.9rem;
  background-color: #222b37;
  color: #fff;
  padding: 3px 6px;
  border-radius: 3px;
  border: 1px solid #222b37;
  transition: 0.2s ease;
}
.content-top .abstract-wrapper .high-level-tag-wrapper .high-level-tag:hover {
  color: #222b37;
  background-color: #fff;
}
.content-top .abstract-wrapper .high-level-tag-wrapper .secondary {
  background-color: #fff;
  color: #222b37;
}
.content-top .abstract-wrapper .high-level-tag-wrapper .secondary:hover {
  color: #fff;
  background-color: #222b37;
}
.content-top .files-wrapper {
  grid-column: 1/span 2;
  border-left: 1px solid #c1c1c1;
  width: 25%;
}
@media only screen and (min-width: 35em) {
  .content-top .files-wrapper {
    grid-column: 2/span 1;
    grid-row: 2;
  }
}
@media only screen and (min-width: 60.25em) {
  .content-top .files-wrapper {
    grid-column: 3/span 1;
    grid-row: 1;
  }
}
.content-top .files-wrapper .files {
  padding: 1em;
}
.content-top .files-wrapper .element-list {
  height: 6.5rem;
  overflow: hidden;
}
.content-top .files-wrapper .element-list .extras {
  margin-left: 5px;
}
.content-top .files-wrapper ul {
  list-style-type: none;
  margin: 0 0 0 1em;
}
.content-top .files-wrapper ul li {
  max-width: 12.5em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.content-top .files-wrapper ul li img {
  margin-right: 0.2em;
}
.content-top .files-wrapper .more-files {
  margin: 0.5em;
  text-align: center;
}

.content-bottom {
  grid-column: 1/span 4;
  grid-row: 3;
  display: flex;
  flex-flow: row;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: repeat(3, auto);
  border-top: 1px solid #c1c1c1;
  margin: 1em 0;
}
@media only screen and (min-width: 60.25em) {
  .content-bottom {
    grid-template-columns: 19rem auto;
    grid-template-rows: auto;
  }
}
.content-bottom .content-wrapper {
  display: flex;
  flex-flow: row;
  grid-row: 2;
  background-color: #fff;
  width: 60%;
}
@media only screen and (min-width: 60.25em) {
  .content-bottom .content-wrapper {
    grid-column: 2/span 1;
    grid-row: 1;
  }
}
.content-bottom .content-display {
  padding: 1em;
}
.content-bottom .content-menu {
  grid-row: 1;
  width: 25%;
  border-left: 1px solid #c1c1c1;
  border-right: 1px solid #c1c1c1;
  margin-top: 1em;
}
@media only screen and (min-width: 60.25em) {
  .content-bottom .content-menu {
    grid-column: 1/span 1;
    grid-row: 1;
  }
}
.content-bottom .content-menu ul {
  list-style-type: none;
  margin: 0 2em;
}
.content-bottom .content-menu ul li {
  border-bottom: 1px solid #c1c1c1;
  padding-bottom: 0.5em;
}
.content-bottom .content-menu ul li .active {
  font-weight: bold;
}
.content-bottom .content-menu ul li .active:hover {
  margin-left: 1em;
}
.content-bottom .content-menu ul li .fc-caret-right svg {
  fill: #393c3d;
  height: 0.9rem;
}
.content-bottom .content-menu ul li a {
  color: #393c3d;
  margin-left: 1em;
  transition: 0.2s ease;
}
.content-bottom .content-menu ul li a:hover {
  margin-left: 1.5em;
}

@supports (display: grid) {
  .title-wrapper .cta-wrapper {
    width: auto;
  }
  .content-top .image-wrapper {
    width: auto;
  }
  .content-top .abstract-wrapper {
    width: auto;
  }
  .content-top .files-wrapper {
    width: auto;
  }
  .content-bottom .content-wrapper {
    width: auto;
  }
  .content-bottom .content-wrapper .content-display {
    width: auto;
  }
  .content-bottom .content-menu {
    width: auto;
  }
}
ol.tags {
  margin: 0;
  padding: 0;
}

.top.tags {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  list-style-type: none;
  margin: 0;
  margin-left: -10px;
  padding: 0;
  /* Main list */
}
.top.tags li {
  display: flex;
  margin: 0;
}
.top.tags .sub-tags {
  display: flex;
}
.top.tags .sub-tags > .tag {
  background: #CCCFE0;
}
.top.tags .tag {
  background: #D8DBE8;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  margin-left: -10px;
  border: 3px solid #ffffff;
  color: #40476D;
  cursor: pointer;
  padding: 4px 10px 4px 12px;
  position: relative;
  transition: 0.2s;
}
.top.tags .tag:hover {
  background-color: #40476D;
  border: 3px solid #ffffff;
  color: #D8DBE8;
  transform: scale(1.05);
}
.top.tags .tag.admin {
  display: none;
}
.top.tags .top-level {
  margin-left: 1em;
}
.top.tags .top-level > .tag {
  background: #BFC3D9;
  border: 3px solid #ffffff;
  padding-left: 10px;
}
.top.tags .top-level > .tag:hover {
  background: #40476D;
  border: 3px solid #ffffff;
  color: #D8DBE8;
  transform: scale(1.05);
}

.top > .sub-tags:hover .tag {
  background: #40476D;
  background-image: unset;
  color: #D8DBE8;
}

a.instructor-access {
  width: 100%;
  margin: 1em 0em;
  background: #7f2f31;
}